.callus {
    margin-top: 2px;
    position:absolute;
    width:30px;
    height:50px;
    cursor:pointer;
    }
    .callus i {
    color:rgb(44, 44, 218);
    transition:all 1s ease-out
    }
    .callus:hover i {
    -webkit-transform:scale(1.3);
            transform:scale(1.3);
    opacity:0.8;
    transition:all 1s ease-out
    }
